<template>
    <!-- Needs to be a class! -->
    <div class="shop">
        
        <div id="product-container">
            <product v-for="index in 10" :key="index"/>
        </div>
        
        <div id="page-selector">
            
            <img src="@/assets/left.svg">
            
            <p @click="toPage(1)">1</p>
            <p>2</p>
            <p>3</p>
            <p>...</p>
            <p>8</p>
            
            <img src="@/assets/right.svg">
            
        </div>
        
    </div>
</template>

<script>
import Product from '@/components/product/Product.vue'

export default {
    name: 'home',
    
    components: {
        Product
    },
    
    methods: {
        toPage(index) {
            index;
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
            
            /* Reload the the next page here! */
        }
    }
}
</script>

<style>

#product-container
{
    display: grid;
    
    width: 80vw;
    
    grid-template-columns: 1fr 1fr 1fr 1fr;
    
    gap: 16px;
}

#page-selector
{
    height: 24px;
    
    display: flex;
    flex-direction: row;
    align-items: center;
    
    gap: 8px;
    
    user-select: none;
}

#page-selector > *
{
    cursor: pointer;
}

#page-selector > p
{
    margin: 0;
}

@media (orientation: portrait) {
    #product-container
    {
        grid-template-columns: 1fr 1fr;
        width: 100%;
    }
}

</style>